@charset "utf-8";
@import url('https://fonts.googleapis.com/css?family=Roboto|Rubik&display=swap');

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  box-sizing: border-box !important;
  // background: #f9fbfe !important;
  background: rgb(243, 242, 241);  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #FFFFFF, rgb(247, 244, 242));  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #FFFFFF, rgb(245, 242, 239)); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  font-family: Rubik, "Noto Sans JP", sans-serif !important;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.b-wrapper {
  flex: 1;
}
